:root {
    --allcolor: rgba(255, 166, 0, 0.23);
    --sidebarcolor: orange;
}

html {
    font-size: 16px;
}

body {
    margin: 0;
    padding: 0;
    background-color: var(--allcolor);
    font-family: 'Tai Heritage Pro', serif;
}

.heading {
    font-size: 3rem;
    text-align: center;
    text-decoration: underline;
}

ul {
    font-size: 1.5rem;
}

.sideEdu {
    justify-content: center;
    width: 2px;
    height: 50rem;
}

.rocket {
    z-index: 1;
    width: 2rem;
    position: sticky;
    transform: rotateZ(180deg) translateX(20px);
    top: 100px;
    right: 0;
}

.eduIcon {
    padding-bottom: 9rem;
    transform: translateX(20px);
}

.box3 {
    display: flex;
    justify-content: space-around;
}


/* images css start here */

#ham,
#cross {
    font-size: 2rem;
    color: brown;
    margin: 0.5rem;
    position: absolute;
    cursor: pointer;
}

#cross {
    left: 12.5vw;
    top: 2px;
}

.containers {
    display: flex;
    width: 100%;
    height: 100vh;
    box-sizing: border-box;
}

.slidebar nav ul {
    list-style: none;
    margin: 32px;
    padding: 0;
    animation: up 1s forwards;
}

@keyframes up {
    0% {
        transform: translateY(500px);
    }
    100% {
        transform: translateY(0px);
    }
}

.slidebar nav {
    margin-top: 50%;
}

.slidebar nav ul li {
    padding: 1vh 0;
}

.slidebar nav ul li a {
    text-decoration: none;
    color: white;
}

.slidebar nav ul li a:hover {
    /* text-decoration: none; */
    color: rgb(5, 226, 45);
}

.slidebar {
    background-color: var(--sidebarcolor);
    font-size: 2vw;
    min-width: 15vw;
    height: 100%;
    display: flex;
    justify-content: center;
    font-family: 'Oswald', sans-serif;
}

.mainContant {
    /* background-color: greenyellow; */
    width: 100%;
    height: 100%;
    /* display: flex; */
    justify-content: center;
    align-items: center;
    overflow-y: scroll;
}


/* Introduction css start here */

.box2 {
    width: 100%;
    height: 50%;
    box-sizing: border-box;
    padding: 1rem;
}

.firstBox {
    display: grid;
    grid-template-areas: "mypic name name name" "mypic info info info";
}


/* image side in my name */

.name {
    grid-area: name;
}

.mypic {
    grid-area: mypic;
    border-radius: 50%;
    position: relative;
    top: -69px;
    left: 28px;
}

.info {
    grid-area: info;
}

.box1 {
    width: 100%;
    min-height: 100%;
    padding: 1rem;
    background-color: rgb(252, 246, 237);
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
}

.box1:nth-child(even) {
    background-color: rgb(251, 240, 225);
    display: flex;
    justify-content: center;
}

.box1 h1 {
    font-size: 4vw;
    color: rgb(0, 0, 0);
    margin: 0;
    text-align: center;
}

.box1 h3 {
    margin: 0;
    font-size: 1rem;
    text-align: center;
    text-transform: uppercase;
    color: rgb(8, 8, 8);
}

.box1 p {
    /* margin: 0; */
    font-size: 1rem;
    /* text-emphasis: initial; */
    text-align: center;
}

.box1 span {
    color: rgb(172, 54, 54);
}

.btn {
    border: 2px solid black;
    padding: 9px 14px;
    border-radius: 22px;
    cursor: pointer;
    background-color: white;
    margin: 5px;
}

.btn a {
    text-decoration: none;
}

#profile_pic {
    border: 5px solid rgb(184, 146, 73);
    background: url(./myPic.png) 0px 0px;
    background-size: 10vw;
    background-repeat: no-repeat;
    background-color: rgb(215, 207, 207);
    border-radius: 50%;
    width: 10vw;
    height: 10vw;
    transform: translateY(-200px);
    animation: imageMove 1s 1 forwards;
}

@keyframes imageMove {
    100% {
        transform: translateX(0px);
    }
}

.profile_pos {
    /* style={{width:"100%",display:"flex", justifyContent:"center", margin:"0"}} */
    width: 100%;
    display: flex;
    justify-content: center;
    margin: 0;
}


/* Contact page css start here */

::placeholder {
    color: rgb(53, 120, 178);
}

.sec {
    border: 2px solid violet;
    border-radius: 10px;
    background-color: aquamarine;
    padding: 2%;
    width: 100%;
    margin: 10vw;
    text-align: center;
}

.rows {
    display: flex;
    background-color: aqua;
    justify-content: space-evenly;
}

.forms {
    /* border: 2px solid black; */
    padding: 10px;
    width: 50%;
}

.forminput {
    padding: 5px 15px;
    /* border: 2px solid rgb(255, 255, 255); */
    background: none;
    border-radius: 20px;
    outline: none;
    /* border: none; */
    width: 100%;
}

form span {
    position: absolute;
    color: white;
    margin: 5px;
    transform: translateX(20px) translateY(-18px);
    display: none;
}

form span::before {
    content: "";
    /* background-color: aqua; */
    width: 120%;
    height: 50%;
    z-index: -1;
    position: absolute;
    transform: translateX(-5px) translateY(10px);
    /* display: none; */
}

.formcontainer {
    /* border: 2px solid black; */
    margin: 10px;
}


/* footer style here */

.footer {
    position: static;
    bottom: 0rem;
    background-color: rgb(129, 36, 36);
}

.footer p {
    margin: 0;
    padding: 2vh;
    text-align: center;
    color: white;
    background-color: rgb(82, 79, 79);
}


/* skill Progress css */

@property --pgPercentage {
    syntax: '<number>';
    inherits: false;
    initial-value: 0;
}

div[role="progressbar"] {
    --size: 10rem;
    --pgPercentage: var(--value);
    width: var(--size);
    height: var(--size);
    border-radius: 50%;
    display: grid;
    place-items: center;
    background: radial-gradient(closest-side, rgba(255, 255, 255, 0.875) 80%, transparent 0 99.9%, white 0), conic-gradient(var(--fg) calc(var(--pgPercentage) * 1%), var(--bg) 0);
    font-family: Helvetica, Arial, sans-serif;
    font-size: calc(var(--size) / 5);
    color: var(--fg);
    animation: growProgressBar 3s infinite;
}


/* div[role="progressbar"]:hover {
    transition: all 5s;
    --pgPercentage: var(--value);
} */

@keyframes growProgressBar {
    0% {
        --pgPercentage: 0;
    }
    100% {
        --pgPercentage: var(--value);
    }
}

div[role="progressbar"]::before {
    counter-reset: percentage var(--value);
    content: counter(percentage) '%';
}

.progressCircle {
    display: flex;
    align-items: center;
    gap: 1rem;
    margin: 1rem;
}

.progressCircle span {
    font-size: 1.5rem;
}

.skillName {
    text-align: center;
}

.skillBox {
    display: grid;
    justify-content: center;
    gap: 5rem;
    grid-template-columns: auto auto auto auto;
}


/* media queriy start here  */

@media screen and (max-width: 394px) {
    html {
        font-size: 13px;
    }
    body {
        background-color: rgb(226, 229, 242);
    }
    .eduIcon {
        display: none;
    }
    .rocket {
        transform: rotateZ(-45deg) translateY(-10px);
    }
    .box3 {
        display: flex;
        justify-content: flex-start;
    }
    /* skill css */
    .skillBox {
        display: grid;
        justify-content: center;
        gap: 2rem;
        grid-template-columns: auto auto auto;
    }
    div[role="progressbar"] {
        --size: 6rem;
    }
    .progressCircle span {
        font-size: 1.2rem;
    }
    /*image in info*/
    .name {
        top: 0px;
        right: 220px;
    }
    .mypic {
        width: 300px;
        top: -10px;
        left: -120px;
    }
    .info {
        top: 0px;
        right: 220px;
    }
    #ham,
    #cross {
        top: 0px;
        margin: 0;
        filter: invert();
    }
    #cross {
        left: 90vw;
        padding: 2px;
    }
    #ham {
        position: fixed;
        z-index: 0;
        padding: 2px;
        background-color: rgba(161, 9, 9, 0.288);
        width: 100%;
    }
    .containers {
        display: block;
        padding: 0px;
        margin: 0;
        width: 100%;
        height: 100%;
    }
    .mainContant {
        overflow: none;
        width: 100%;
        display: block;
    }
    .slidebar nav ul li a {
        /* margin: ; */
        text-decoration: none;
        color: rgb(240, 232, 232);
    }
    .slidebar nav ul li a:hover {
        color: rgba(27, 228, 64, 0.603);
    }
    .slidebar {
        background-color: rgba(83, 74, 208, 0.952);
        font-size: 5vw;
        width: 100%;
        height: fit-content;
        display: inline-block;
    }
    .slidebar nav ul {
        list-style: none;
        text-align: left;
        width: 50%;
    }
    .slidebar nav ul li {
        padding: 1vw 0;
    }
    .slidebar nav {
        padding: 0;
        margin: 0;
        width: 60%;
    }
    .box1 {
        width: 100%;
        height: fit-content;
        display: inline-flex;
        color: black;
        padding: 2rem;
    }
    .box2 {
        padding: 0;
        padding-top: 2rem;
    }
    .box2 ul {
        font-size: 0.8rem;
    }
    .heading {
        font-size: 2rem;
    }
    #profile_pic {
        width: 8rem;
        height: 8rem;
        border: 2px solid white;
        background-position: 4px 0px;
        background-size: 8rem;
        transform: scale(2);
    }
    .profile_pos {
        position: relative;
        top: 150px;
        left: 150px;
        width: 100%;
        height: 100%;
        margin: 0 auto;
        margin: 0;
    }
}