:root {
    --a: 5rem;
}

.contain {
    perspective: 900px;
    width: 100%;
    height: calc(2*var(--a));
    /* background: rgb(29, 28, 28); */
    display: flex;
    justify-content: center;
    padding-bottom: 10px;
    /* grid-template-columns: 50% 50%; */
    /* grid-template-rows: 50% 50%; */
    /* overflow: hidden; */
}

.seprator {
    width: auto;
    height: auto;
    /* padding: auto; */
    display: flex;
    justify-content: center;
    align-items: center;
    /* border: 2px solid rgb(0, 0, 0); */
}

.box {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100px;
    height: 100px;
    /* border: 5px solid lime; */
    border-radius: 50%;
    transform-style: preserve-3d;
    /* transform: rotate(45deg); */
    animation: move 5s linear infinite;
}

.glowBox {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    /* border: 5px solid lime; */
    transform-style: preserve-3d;
    /* transform: rotateZ(45deg) translateZ(100px); */
    animation: rotate 5s linear infinite;
}

.cube {
    transform-style: preserve-3d;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 300px;
    height: 300px;
    transform: rotateX(-35deg) rotateY(0deg) rotateZ(45deg);
}

.glowCube {
    transform-style: preserve-3d;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 300px;
    height: 300px;
    /* animation: rotate 5s linear infinite; */
    transform: rotateX(0deg) rotateY(45deg) rotateZ(0deg);
}

@keyframes rotate {
    0% {
        transform: rotateX(-20deg) rotateY(0deg);
    }
    100% {
        transform: rotateX(-20deg) rotateY(360deg);
    }
}

@keyframes move {
    0% {
        transform: rotateY(360deg);
    }
    100% {
        transform: rotateY(0deg);
    }
}

.face {
    transform-style: preserve-3d;
    position: absolute;
    width: var(--a);
    height: var(--a);
    opacity: 1;
    display: grid;
    grid-template-columns: auto auto auto;
    background: var(--color);
}

.face2 {
    transform-style: preserve-3d;
    position: absolute;
    width: var(--a);
    height: var(--a);
    opacity: 1;
    display: grid;
    grid-template-columns: auto auto auto;
    /* border: 2px solid white; */
    background: linear-gradient(black 20%, rgb(41, 210, 41));
}

.face2.bottom::before {
    content: '';
    position: absolute;
    width: var(--a);
    height: var(--a);
    background: rgba(50, 239, 50, 1);
    box-shadow: 0px 0px 15px 0.1px rgba(50, 239, 50, 1);
    transform: translateZ(-10px) translateY(0px) rotateZ(90deg);
}

