.front {
    transform: translateZ(calc(var(--a)/2));
}

.back {
    transform: translateZ(calc(-1*var(--a)/2));
}

.top {
    transform: translateY(calc(-1*var(--a)/2)) rotateX(90deg);
}

.bottom {
    transform: translateY(calc(var(--a)/2)) rotateX(90deg);
    /* transform: ; */
}

.left {
    transform: translateX(calc(-1*var(--a)/2)) rotateY(90deg);
}

.right {
    transform: translateX(calc(var(--a)/2)) rotateY(90deg);
}